import { Button, ButtonGroup, Dialog } from "@mui/material";
import React, { useState, useRef } from "react";

import ClearIcon from '@mui/icons-material/Clear';
import ReplayIcon from '@mui/icons-material/Replay';
import InsertPhotoIcon from '@mui/icons-material/InsertPhoto';
import PhotoCameraIcon from '@mui/icons-material/PhotoCamera';

const env = window.kcapp.env;
const defaultAvatar = env.settings.placeholder_image;
let init_state = {
    avatar: defaultAvatar,
    imgStyle: {
        display: 'block',
        margin: '0 auto',
        maxWidth: '100%'
    },
    accept: ".jpg, .jpeg, .gif, .png",
    inputName: "file",
    actionName: "file_action",
    actionValue: "none", // values: none, remove, update
    buttonContainerStyle: {
        display: 'flex',
        alignItems: 'center',
        flexDirection: 'column',
        marginTop: '1em'
    },
    buttonGroupProps: {
        variant: "contained",
        color: "inherit",
        size: "large"
    },
    // camera capture
    stream: null,
    camera: false
};
/**
Example:
<InputImage 
    inputName="file"
    actionName="file_action"
    input_size="small"
    avatar="URL"
    accept=".jpg, .jpeg, .gif, .png"
    imgStyle={{}}
/>

props:
    actionName="file_action"
        Optional. Name of the input hidden. Default value is "file_action"
    actionValue="[none|remove|update]"
        Optional. Value of the input hidden. Default value is "none"
    avatar="URL"
        Optional. Image URL. Default value is null
    imgStyle={OBJ} 
        Optional. Object that describes the style for the image. Default value is { display: 'block', margin: '0 auto', maxWidth: '100%' }
    input_size="[small|medium|large]"
        Optional. Size of the buttons. Default value is "large"
    inputName="INPUT_NAME" 
        Optional. Name of the input file. Default value is "file"
    accept=".jpg, .jpeg, .gif, .png"
        Optional. String that describe the file types accepted by the input file. Default value is ".jpg, .jpeg, .gif, .png"
 */
class InputImage2 extends React.Component {
    constructor(props) {
        super(props);

        this.inputRef = React.createRef();

        let newState = {
            ...init_state,
            ...props
        };
        newState.avatarOriginal = newState.avatar;
        if (newState.avatar == null) {
            newState.avatarOriginal = defaultAvatar;
            newState.avatar = defaultAvatar;
        }
        this.state = newState;

        this.player = null;

        window.p = this;
    }

    selectClick = (evt) => {
        this.inputRef.current.click();
    }

    removeClick = (evt) => {
        this.setState({
            avatar: defaultAvatar,
            actionValue: "remove"
        });
    }

    restoreClick = (evt) => {
        this.setState({
            avatar: this.state.avatarOriginal,
            actionValue: "none"
        });
    }

    captureClick = async (evt) => {
        try {
            let stream = await navigator.mediaDevices.getUserMedia({
                audio: false,
                video: {
                    facingMode: "environment"
                }
            });
            this.setState({
                stream: stream,
                camera: true
            });
        } catch (err) {
            window.alert(err);
        }

    }

    canUseMediaDevice = () => {
        return (navigator.mediaDevices && navigator.mediaDevices.getUserMedia);
    }

    inputChange = (evt) => {
        if (evt.target.files.length == 0) {
            this.restoreClick();
            return;
        }

        let file = evt.target.files[0];
        let reader = new FileReader();
        let url = reader.readAsDataURL(file);
        reader.onloadend = (e) => {
            this.setState({
                avatar: reader.result,
                actionValue: "update"
            });
        };
    }

    render() {
        const st = this.state;
        const props = this.props;
        const sxActionButtons = {
            '& > span.MuiButton-startIcon': {
                mr: 0
            }
        };

        const input_size = props.input_size == undefined ? "large" : props.input_size;

        return (
            <React.Fragment>
                <img alt=""
                    src={st.avatar}
                    data-default={defaultAvatar}
                    data-original={st.avatar}
                    style={st.imgStyle}
                />
                <input
                    ref={this.inputRef}
                    type="file"
                    name={st.inputName}
                    accept={st.accept}
                    data-role="avatar_file"
                    style={{ display: 'none' }}
                    onChange={this.inputChange}
                />
                <input
                    type="hidden"
                    name={st.actionName}
                    value={st.actionValue}
                />
                <div style={st.buttonContainerStyle}>
                    <ButtonGroup {...st.buttonGroupProps}>
                        {
                            this.canUseMediaDevice() ? (
                                <Button
                                    size={input_size}
                                    startIcon={<PhotoCameraIcon />}
                                    sx={sxActionButtons}
                                    onClick={this.captureClick}
                                    title="Capture" />
                            ) : null
                        }

                        <Button
                            size={input_size}
                            startIcon={<InsertPhotoIcon />}
                            sx={sxActionButtons}
                            onClick={this.selectClick}
                            title="Upload" />
                        <Button
                            size={input_size}
                            startIcon={<ReplayIcon />}
                            sx={sxActionButtons}
                            onClick={this.restoreClick}
                            title="Restore"
                        />
                        <Button
                            size={input_size}
                            startIcon={<ClearIcon />}
                            sx={sxActionButtons}
                            onClick={this.removeClick}
                            color="error"
                            title="Remove"
                        />
                    </ButtonGroup>
                </div>

                {
                    <Dialog
                        open={st.camera}
                        fullScreen
                    >
                        <video
                            autoPlay
                            style={{
                                width: '100%',
                                height: '100%'
                            }}
                            ref={(videoref) => {
                                this.player = videoref;
                                this.player.srcObject = st.stream;
                            }}
                        />
                    </Dialog>
                }
            </React.Fragment>
        );
    }
}

export default InputImage2;